import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '搜索商品' //导航栏文字
			},
			searchGoodsPage: {}, //商品搜索数据
			searchList: [], //商品搜索列表
			params: {
				page: 1,
				size: 10,
				keyWord: '' //搜索关键字
			}
		}
	},
	onLoad(options) {
		this.params.keyWord = options.keyword
		this.initData()
	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.searchList.length >= this.searchGoodsPage.total) {
			return;
		}
		this.params.page++;
		this.getPageGoodsList()
	},
	methods: {
		search() {
			this.initData();
		},
		shopDetailPage(item) {
			this.$store.commit('setGoodsDetail', item)
			this.$turnPage('/pages/index/business/shop-detail?spuId='+item.spuId, 'navigateTo')
		},
		initData() {
			//重置分页参数
			this.specialGoodsData = {}
			this.specialGoodsList = []
			this.params.page = 1
			this.getPageGoodsList();
		},
		getPageGoodsList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.pageGoodsList, this.params).then((res) => {
				that.searchGoodsPage = res.data
				if(that.params.page == 1) {
					that.searchList = res.data.data
				} else {
					that.searchList = that.searchList.concat(res.data.data)
				}
			});
		},
	}
}
